import React from 'react'
import { Card, CardMedia, CardContent, CardActions, Typography, IconButton } from '@material-ui/core';
import {AddShoppingCart} from '@material-ui/icons'
import useStyles from './styles';

/*--------------------------------------------------------------------/*
/*Everuy Product is set into card in this script and passed it to Products,jsx*/
/*--------------------------------------------------------------------*/

const Product = ({ product,onAddToCart }) => {
    const classes = useStyles();
    return (
        <Card className={classes.root}>
            <CardMedia className={classes.media} image={product.media.source} title={product.name}/>
            <CardContent>
                <div className={classes.cardContent}>
                    <Typography variant="h6" gutterBottom>
                         {product.name}
                    </Typography>
                    <Typography variant="h6" >
                         {product.price.formatted_with_symbol}
                    </Typography>
                </div>
                <Typography dangerouslySetInnerHTML={{ __html: product.description }} variant="body2"  component="p" />
            </CardContent>
            <CardActions disableSpacing className={classes.cardActions}>
                <IconButton className={ classes.icon} arial-label="Add to Cart" onClick={()=> onAddToCart(product.id,1)}>
                    <AddShoppingCart />
                 </IconButton>
            </CardActions>
        </Card> 
    )
}

export default Product
