import React from 'react'
import { Container, Typography, Button, Grid } from '@material-ui/core';
import useStyles from './styles';
import CardItem from './CartItem/CartItem'
import {Link} from 'react-router-dom'

const Cart = ({ cart,handleUpdateCartQty, handleRemoveFromCart,handleEmptyCart }) => {
    
    const classes = useStyles();
 
    const EmptyCart = () => (
        <main className={classes.empty}>
        <Typography variant="subtitle1">You have no item in card.  
        <Link to="/" className={classes.link}>Add Items to Cart</Link> 
        </Typography>
        </main>
    );

    const FilledCart = () => (
        <>
            
            <Grid  container spacing={3}>
                {cart.line_items.map((item) => (
                    <Grid  item xs={12} sm={4} key={item.id}>
                        <CardItem item={item}
                            onUpdateCartQty={handleUpdateCartQty}
                            onRemoveCartQty={handleRemoveFromCart}
                        />
                    </Grid>
                ))}
            </Grid>
            <div className= {classes.cardDetails} >
                <Typography  variant="h5">
                    Subtotal: {cart.subtotal.formatted_with_symbol}
                    
                  
                </Typography>
                <div className={classes.cardBtn} >
                    <Button className={classes.emptyButton} size="large" type="button" variant="contained" color="secondary" onClick={handleEmptyCart}>Empty cart</Button>
                    <Button component={Link} to="/checkout" className={classes.checkoutButton} size="large" type="button" variant="contained" color="primary">Check Out</Button>
                    
                </div>
            </div>
       </>    
    )
    if (!cart.line_items) return 'Loading..';
    return (
    
        <main className={classes.main}>

        <Container classame={ classes.bg}>
            <div className={classes.toolbar} />
            <Typography className={classes.title} variant="h3" gutterBottom>Your Shopping Cart</Typography>
            {!cart.line_items.length? <EmptyCart /> : <FilledCart />}   
        </Container>
        </main>
       


    )
}

export default Cart
